import React from "react";
import { Member } from "../types/Member";
import MemberCard from "./MemberCard";
import styles from "../styles/MemberList.module.css"; //   

export interface MemberListProps {
  members: Member[];
  onAddMemberClick?: () => void;
  onDeleteMember?: (memberId: number) => void;
  onAddMember?: (memberId: string) => void;
}

const MemberList: React.FC<MemberListProps> = ({
  members,
  onAddMemberClick,
  onDeleteMember,
  onAddMember,
}) => {
  //   
  const handleDragOver = (e: React.DragEvent<HTMLDivElement>) => {
    e.preventDefault();
  };

  const handleDrop = (e: React.DragEvent<HTMLDivElement>) => {
    e.preventDefault();
    if (!onAddMember) return;
    const memberId = e.dataTransfer.getData("memberId");
    if (memberId) onAddMember(memberId);
  };

  return (
    <div
      className={styles.container}
      onDragOver={handleDragOver}
      onDrop={handleDrop}
    >
      {/*  */}
      <div className={styles.header}>
        <h3 className={styles.title}> </h3>
        {onAddMemberClick && (
          <button className={styles.addButton} onClick={onAddMemberClick}>
            
          </button>
        )}
      </div>

      {/*    */}
      <div className={styles.list}>
        {members.map((member) => (
          <div key={member.id} className={styles.cardWrapper}>
            <MemberCard
              member={member}
              onDragStart={(e) =>
                e.dataTransfer.setData("memberId", member.id.toString())
              }
            />
            {onDeleteMember && (
              <button
                className={styles.deleteButton}
                onClick={() => onDeleteMember(member.id)}
              >
                
              </button>
            )}
          </div>
        ))}
      </div>
    </div>
  );
};

export default MemberList;
