import React, { useState, useEffect, useRef } from "react";
import '../styles/ChatBox.css';

type Message = UserMessage | SystemMessage | DateSeparator;

interface UserMessage {
  type: 'user';
  id: number;
  user: { name: string; avatarInitial: string };
  text: string;
  time: string;
  isMe: boolean; // ''  
}
interface SystemMessage {
  type: 'system';
  id: number;
  text: string;
}
interface DateSeparator {
  type: 'date';
  id: number;
  date: string;
}

const initialMessages: Message[] = [
  { type: 'date', id: 1, date: '2025 01 01' },
  { type: 'system', id: 2, text: ' !' },
  { type: 'user', id: 3, user: { name: '', avatarInitial: '' }, text: '!  .', time: ' 06:15', isMe: false },
  { type: 'user', id: 4, user: { name: '', avatarInitial: '' }, text: 'API   .  .', time: ' 07:30', isMe: false },
  { type: 'date', id: 5, date: '2025 10 11' },
  { type: 'user', id: 6, user: { name: '', avatarInitial: '' }, text: '.pdf', time: ' 03:53', isMe: true },
];

const ChatBox: React.FC = () => {
  const [messages, setMessages] = useState<Message[]>(initialMessages);
  const [input, setInput] = useState("");
  const messageEndRef = useRef<HTMLDivElement>(null);

  //       
  useEffect(() => {
    messageEndRef.current?.scrollIntoView({ behavior: "smooth" });
  }, [messages]);

  const handleSend = () => {
    if (input.trim() === "") return;

    const newMessage: UserMessage = {
      type: 'user',
      id: Date.now(),
      user: { name: "", avatarInitial: '' },
      text: input,
      time: new Date().toLocaleTimeString([], { hour: "2-digit", minute: "2-digit" }),
      isMe: true,
    };

    setMessages([...messages, newMessage]);
    setInput("");
  };

  return (
    <div className="chat-container">
      {/*  */}
      <div className="chat-header">
        <span className="online-indicator"></span>
        <h3 className="chat-header-title"> </h3>
        <p className="chat-header-subtitle">  </p>
      </div>

      {/*   */}
      <div className="message-list">
        {messages.map((msg) => {
          //      UI 
          switch (msg.type) {
            case 'date':
              return <div key={msg.id} className="message-separator"><span>{msg.date}</span></div>;
            case 'system':
              return <div key={msg.id} className="system-message">{msg.text}</div>;
            case 'user':
              return (
                <div key={msg.id} className={`message-row ${msg.isMe ? 'is-me' : ''}`}>
                  <div className="avatar">{msg.user.avatarInitial}</div>
                  <div className="message-info">
                    {!msg.isMe && <div className="username">{msg.user.name}</div>}
                    <div className="message-content">
                      <div className="message-bubble">{msg.text}</div>
                      <div className="timestamp">{msg.time}</div>
                    </div>
                  </div>
                </div>
              );
            default:
              return null;
          }
        })}
        {/*     div */}
        <div ref={messageEndRef} />
      </div>

      {/*  */}
      <div className="input-area">
        <button className="icon-button">
          <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" strokeWidth={1.5} stroke="currentColor" style={{ width: '24px', height: '24px' }}>
            <path strokeLinecap="round" strokeLinejoin="round" d="M7.217 10.907a2.25 2.25 0 1 0 0 2.186m0-2.186c.18.324.283.696.283 1.093s-.103.77-.283 1.093m0-2.186 9.566-5.314m-9.566 7.5 9.566 5.314m0 0a2.25 2.25 0 1 0 3.935 2.186 2.25 2.25 0 0 0-3.935-2.186Zm0-12.814a2.25 2.25 0 1 0 3.933-2.185 2.25 2.25 0 0 0-3.933 2.185Z" />
          </svg>
        </button>
         <button className="icon-button">
          <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" strokeWidth={1.5} stroke="currentColor" style={{ width: '24px', height: '24px' }}>
            <path strokeLinecap="round" strokeLinejoin="round" d="M15.182 15.182a4.5 4.5 0 0 1-6.364 0M21 12a9 9 0 1 1-18 0 9 9 0 0 1 18 0ZM9.75 9.75c0 .414-.168.75-.375.75S9 10.164 9 9.75 9.168 9 9.375 9s.375.336.375.75Zm-.375 0h.008v.015h-.008V9.75Zm5.625 0c0 .414-.168.75-.375.75s-.375-.336-.375-.75.168-.75.375-.75.375.336.375.75Zm-.375 0h.008v.015h-.008V9.75Z" />
          </svg>
        </button>
        <textarea
          rows={1}
          placeholder=" ..."
          className="input-field"
          value={input}
          onChange={(e) => setInput(e.target.value)}
          onKeyDown={(e) => {
            if (e.key === "Enter" && !e.shiftKey) {
              e.preventDefault();
              handleSend();
            }
          }}
        />
        <button onClick={handleSend} className="send-button"></button>
      </div>
    </div>
  );
};

export default ChatBox;